.meeting-room-page {
    padding: 0 2.4rem;
    position: relative;
    &__uc {
        min-height: 60rem;
        display: flex;
        align-items: center;
        justify-content: center;
        @include susy-breakpoint($s-320, $c8) {
            min-height: 40rem;
        }
    }
    &__uc-msg {
        img {
            width: 33rem;
            display: block;
            margin: 0 auto 2rem;
            @include susy-breakpoint($s-320, $c8) {
                width: 23rem;
            }
        }
        p {
            text-align: center;
            font-size: var(--font-size-14);
            color: var(--grey-color-52);
            line-height: 1.6;
        }
    }
}

.meeting-room-nav {
    position: absolute;
    background-color: var(--grey-color-dd);
    border-radius: 0.8rem;
    overflow: hidden;
    display: flex;
    padding: 0.8rem;
    gap: 1.6rem;
    box-sizing: border-box;
    right: 2.4rem;
    top: 1.6rem;
    z-index: 10;
    &__btn-link {
        font-size: var(--font-size-14);
        color: var(--grey-color-52);
        padding: 0.8rem 1.6rem;
        background-color: #fff;
        border-radius: 0.4rem;
        white-space: nowrap;
        &.active {
            background-color: var(--sub-color-400);
            color: #fff;
            pointer-events: none;
        }
    }
}

.meeting-room-area {
    $self: &;
    &:not(:last-child) {
        margin-bottom: 4.8rem;
    }
    &.headquarters {
        #{$self}__info {
            background-color: var(--sub-color-100);
        }
    }
    &.yanping {
        #{$self}__info {
            background-color: var(--blue-color-100);
        }
    }
    &__header {
        margin-bottom: 2.4rem;
    }
    &__title {
        @extend %page-title;
    }
    &__wrap {
        display: grid;
        grid-template-columns: repeat(3, 1fr);
        gap: 2.4rem 4.8rem;
    }
    &__item {
        border-radius: 0.8rem;
        overflow: hidden;
    }
    &__img {
        padding: 1.2rem 2rem;
        background-color: var(--grey-color-ec);
        img {
            max-width: 100%;
            display: block;
            margin-bottom: 0;
        }
    }
    &__info {
        padding: 1.2rem 2rem;
        display: flex;
        justify-content: space-between;
        align-items: center;
    }
    &__detail {
        h4 {
            font-weight: 500;
            font-size: var(--font-size-18);
            color: var(--grey-color-52);
            line-height: 1.6;
        }
        p {
            font-size: var(--font-size-14);
            color: var(--grey-color-52);
            line-height: 1.6;
        }
    }
    &__btn-booking {
        width: 8rem;
        height: 4.4rem;
        background-color: var(--grey-color-7e);
        font-size: var(--font-size-14);
        color: #fff;
        display: block;
        border-radius: 0.4rem;
        line-height: 4.4rem;
        text-align: center;
        transition: all 300ms ease;
        &:hover {
            background-color: var(--grey-color-52);
        }
    }
}